

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 100;
  width: 100%;
  padding: 0%;
}

.logoimg{

  align-items: center;
  
  object-fit: contain;
  height: 100px;
  margin-left: 20px;
  display: inline;
}
.inputtext{
  border: none;
  padding: 10px;
  outline-width: 0;
  width: 250px;
  height: 30px;
  
}
.cah{
  display: inline;
  margin-bottom: 0px;
}

.search{
  display: flex;
  flex: 1 1;
  align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 10px;
  height: 40px;
  border: 1px solid #d3d3d3;
  border-radius: 999px;
}
.navicon{

  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15vw;
  margin-right: 80px;
  
}
.fas{
  margin: 2px;
}

.sd{
  text-align: center;
  align-items: center;
  font-weight: 900!important;
  text-transform: inherit!important;
  color: #ff7779!important;
  z-index: 1;
  border: black;
  position:absolute;
  background-color: #f5f5f5;
  width: 100%;
  margin-bottom: 0%;


}
.sdp{
  margin-top: 5px;
  margin-bottom: 5px;
}
.banimg{
  width: 100%;
  height: 350px;
}
.bnrimg{
  width: 100%;
  height: 100%;
}

.getout{

   
     background-color: #000;
    color: #f5f5f5;
    /* padding: 15% 50px 40px; */
    width: 400px;
    height: 550px;
    position:absolute;
    padding-top: 270px;
    padding-right: 30px;
    padding-left: 50px;
    padding-bottom: 5px;
  
   
}
hr{
  margin: 0%;
  
}

.bntgetout{

  background-color: #ff7779;
  color: #f5f5f5;
  font-weight: 600;
  border-radius: 7px;
  height: 40px;
  border: none;
}

.bntgetout:hover{
 color: #ff7779;
 background-color: #f5f5f5;
}



.boximg{
  width: 100%;
  height: 100px;
}

.card{
  width: 30%;
  display: inline-block;
  box-shadow: 4px 4px 4px rgb(83, 83, 97);
  margin: 1.5%;
  margin-top: 100px;
}

.card .read-more:hover{
  font-size: 14px;
}

.box:hover{
  box-shadow: 8px 8px 8px rgb(83, 83, 97);
  transform: scale(1.1);
}

.footer{
  text-align: center;
  background-color: #f5f5f5;
}
